}
.mane{
  column-count: 1;
  column-gap: 0;
  padding:0%;
  margin: 0;
  margin-bottom: 5rem;
}
.navIcon-pos {
  width: 30px;
  height:  30px;
}

}

.error{
  margin: auto;
  margin-top: 2.5rem;
  width: 320px;
  padding: 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  background: #EF665B;
  border-radius: 8px;
  box-shadow: 0px 0px 5px -3px #111;
}