/* src/components/Vault/VaultPage.css */

.vault-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f9f9f9;
  }
  
  .trainer-box {
    background-color: white;
    border: 1px solid silver;
    border-radius: 7px;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    padding: 2rem;
    text-align: center;
    width: 300px;
  }
  
  .trainer-box h2 {
    margin-bottom: 1.5rem;
    font-family: "Playfair Display", serif;
  }
  
  .buttons-container {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .level-button {
    padding: 0.75rem 1rem;
    border: none;
    background-color: #7BD3EA;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
    font-family: Arial, sans-serif;
  }
  
  .level-button:hover {
    background-color: #4a99c9;
  }
  