/* BatteryChargingComponent.css */

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

:root {
  font-size: 16px; /* Base font size */
}

.warming {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #f0f4fd, #d9e4f5);
  padding: 2rem 1rem;
  border-radius: 0.9375rem; /* 15px equivalent */
  box-shadow: 0 0.625rem 1.5625rem rgba(0, 0, 0, 0.1);
  font-family: 'Poppins', sans-serif;
  flex: 1;
}

.warming-title {
  font-size: 1.75rem; /* 28px equivalent */
  margin-bottom: 1.25rem;
  color: #333;
}

.warming-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.battery-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 2rem;
}

.battery {
  position: relative;
  width: 3.125rem; /* 50px equivalent */
  height: 6.25rem; /* 100px equivalent */
}

.battery-body {
  position: relative;
  width: 100%;
  height: 100%;
  border: 0.125rem solid #ccc; /* 2px equivalent */
  border-radius: 0.375rem; /* 6px equivalent */
  overflow: hidden;
  background: #fff;
}

.battery-level {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0%;
  background: linear-gradient(180deg, #00c6ff, #0072ff);
  transition: height 0.5s linear;
}

.battery-cap {
  position: absolute;
  top: -0.625rem; /* -10px equivalent */
  left: 0.78125rem; /* 12.5px equivalent */
  width: 1.5625rem; /* 25px equivalent */
  height: 0.625rem; /* 10px equivalent */
  background: #ccc;
  border-radius: 0.1875rem 0.1875rem 0 0; /* 3px equivalent */
}

.battery-label {
  margin-top: 0.5rem;
  font-size: 1rem; /* 16px equivalent */
  color: #555;
}

.parsed-content {
  font-size: 1.125rem; /* 18px equivalent */
  line-height: 1.6;
  color: #444;
  max-width: 37.5rem; /* 600px equivalent */
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(1.25rem); /* 20px equivalent */
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.parsed-content {
  opacity: 0;
  animation: fadeInUp 1s forwards;
  animation-delay: 1s;
}

/* Media queries for responsiveness */
@media (max-width: 48rem) { /* 768px equivalent */
  :root {
    font-size: 14px; /* Reduce base font size on smaller screens */
  }

  .warming {
    padding: 1.25rem 0.9375rem;
  }

  .warming-title {
    font-size: 1.5rem; /* 24px equivalent */
  }

  .warming-content {
    flex-direction: column;
  }

  .battery-container {
    margin-right: 0;
    margin-bottom: 1rem;
  }

  .battery {
    width: 2.5rem; /* 40px equivalent */
    height: 5rem; /* 80px equivalent */
  }

  .battery-cap {
    left: 0.625rem; /* 10px equivalent */
    width: 1.25rem; /* 20px equivalent */
    top: -0.5rem; /* -8px equivalent */
  }

  .battery-label {
    font-size: 0.875rem; /* 14px equivalent */
  }

  .parsed-content {
    font-size: 1rem; /* 16px equivalent */
    margin-top: 0;
  }
}
