/* General styles */
body {
  font-family: 'Merriweather', serif;
  background-color: #fff;
  color: #000;
  margin: 0;
  padding: 0;
}
/* Styling for .correct and .incorrect */
.correct {
  background-color: lightgreen !important;
}
.incorrect {
  background-color: crimson !important;
}

/* Central part (mane) - using multi-column layout for two-column magazine style */
.mane {
  column-count: 2; /* Create two equal-width columns */
  height: 100%;
  box-sizing: border-box;
  overflow-y: auto; /* Allow scrolling if content overflows */
  margin-bottom: 5rem;
  background: linear-gradient(to right, #fefefe, #f8f9fa); /* Subtle background gradient */
  border-radius: 15px; /* Add rounded corners to the overall section */
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1); /* Gentle shadow for depth */
}

/* Styling for the individual elements inside .mane */
.mane > * {
  background-color: #fff;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15); /* Enhanced shadow for pop */
  border-radius: 12px; /* More pronounced rounding */
  padding: 25px; /* Increased padding for a spacious look */
  margin-bottom: 25px; /* Increased space between elements */
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Add hover effects */
}

/* Text styling for better readability */
.mane h2, .mane h3 {
  color: #333;
  font-family: 'Merriweather', serif; /* Elegant serif font */
  margin-bottom: 15px;
}

.mane p {
  font-family: 'Open Sans', sans-serif; /* Clean sans-serif font for body text */
  font-size: 1rem;
  line-height: 1.6; /* Improve readability */
  color: #555;
  margin-bottom: 15px;
  text-align: justify; /* Align text for a professional look */
}

/* Image styling */
.mane img {
  padding: 0.3rem;
  margin: 0.5rem 0; /* Balanced margin */
  max-width: 100%;
  height: auto; /* Maintain aspect ratio */
  object-fit: cover; /* Ensure images fill their box proportionally */
  border-radius: 10px; /* Rounded corners for images */
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 8px; /* Soft shadow for depth */
}

/* Add captions under images */
.mane img + figcaption {
  text-align: center;
  font-size: 0.9rem;
  color: #666;
  margin-top: 0.5rem;
  font-style: italic;
}

.content {
  /* Ensure the content occupies the full width */
  width: 100%;
}

.navigationL {
  grid-column: 1;
  padding: 0.2rem;
  cursor: pointer;

}
.navigationR {
  grid-column: 3;
  cursor: pointer;
  padding: 0.2rem;
}
.disabled{
    cursor: not-allowed;
    opacity: 0.6;
    transition: all 0.3s ease;
    
}

.disabled:hover {
    color: #aaa;
    border-color: #ccc;
}
.navIcon-pos{
  position: fixed;
  transform: translateY(30vh);
  width: 4%;
  opacity: 0.3;
}
.nav-dir{
  text-align:center;
  font-size: 0.8em;
  font-weight: 900;
  color: whitesmoke;
  text-shadow:
  -1px -1px 0 #000,
   0   -1px 0 #000,
   1px -1px 0 #000,
   1px  0   0 #000,
   1px  1px 0 #000,
   0    1px 0 #000,
  -1px  1px 0 #000,
  -1px  0   0 #000;
}
.navigation:disabled {
  background: #cf0a0a;
  cursor: not-allowed;
}

.mane > h1, h2, h3, h4, h5, h6 {
  color: #000!important;
