}

/* Container styles */
.container {
  background-color: #FDFFC2;
  border: 1px solid var(--border-color);
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 8px;
}

/* Part styles */
.part {
  margin-bottom: 20px;
  border-radius: 8px;
}

.part > table{
  background-color: #F6F0BC;
}
/* Warming section styles */
.warming {
  background: linear-gradient(135deg, #94FFD8, #A3D8FF);
  padding: 15px;
  margin-bottom: 25px;
  border-radius: 12px;
  font-weight: bold;
  color: #2c3e50;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  min-height: 120px;
  overflow: hidden;
}

/* Bunch styles */
.bunch{
  background-color: #FF76CE;
  color: #fff; /* White text color */
  border-radius: 10px;
  padding: 1rem;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  margin: 20px auto;
  max-width: 600px;
  transition: all 0.3s ease;
  line-height: 1.8;
   text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.9);
}

.bunch:hover {
  transform: scale(1.05);
  border: 2px solid black;
}

/* Hint styles */
.hint {
  background-color: #FDFFC2;
  color: #181a20;
  padding: 10px;
  margin-bottom: 15px;
  border-left: 5px solid #FF76CE;
  border-radius: 4px;
  font-style: italic;
}

/* Expandable styles */
.expandable {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.expandable .word {
  font-weight: bold;
  text-decoration: underline;
  cursor: pointer;
}

.expandable .definition {
  display: none;
  position: absolute;
  background-color: #FF76CE;
  color: #fff;
  padding: 10px;
  border-radius: 4px;
  z-index: 1;
  width: max-content;
  max-width: 300px;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.expandable:hover .definition {
  display: block;
}

