/* Button Styles */
/* Button Styles */

button {
  /* Basic Styling */
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  color: #333; /* Text color */
  background-color: transparent;
  border: 2px solid #0072ff; /* Primary border color */
  border-radius: 0.5rem;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

button::after {
  content: '';
  position: absolute;
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 100%;
  border-top: 2px solid #e52e71; /* Second accent border color */
  transform: skewX(-45deg);
  transition: right 0.5s ease;
}

button:hover::after {
  right: 100%;
}

button:hover {
  color: #0072ff; /* Change text color on hover */
}

button:active {
  transform: translateY(2px);
}

button:focus {
  outline: none;
}

