.lesson-page {
  width: 100%;
  display: grid;
  grid-template-columns: 5% auto 5%;
  grid-template-rows: auto;
  overflow: hidden;
  position: absolute;
  gap:10px;
  background-color: #f8f9fa;

}

.lesson-page .content {
  grid-column: 2 / 3; /* Main content area */
