.single-column {
  column-count: 1;
}
.fixed-button {
  position: fixed;
  left: 93%;
  top: 23%;
  z-index: 1000; /* Ensure it appears above other content */
  cursor: progress;
  display: flex;
  flex-direction: column;
  background: 0!important;
  gap: 1rem;
  box-shadow: none;
}
.tipper {
  position: absolute;
  top: -20px;
  opacity: 0;
  background-color: #ffe53b;
  background-image: linear-gradient(147deg, #ffe53b 0%, #ff2525 74%);
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition-duration: 0.2s;
  pointer-events: none;
  letter-spacing: 0.5px;
}

.tipper::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  background-color: #ff2525;
  background-size: 1000%;
  background-position: center;
  transform: rotate(45deg);
  bottom: -15%;
  transition-duration: 0.3s;
}
.fixed{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.151);
  position: relative;
}
.fixed img{
  box-shadow: none;
}
.fixed:hover .tipper {
  top: -40px;
  opacity: 1;
  transition-duration: 0.3s;
}

/* General styles for the tooltip overlay */
.tooltip-overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.6); /* Darker background for clarity */
  display: flex;
  justify-content: center;
  z-index: 1000;
  padding: 10px;
}

/* Tooltip content styles */
.tooltip-content {
  background-color: #fff;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3); /* Enhanced shadow for depth */
  width: 700px; /* Max width with responsive adjustment */
  max-height: 80vh; /* Constrain height */
  overflow-y: auto;
  column-count: 2; /* Vertical column layout */
  column-gap: 20px;
}

/* Header style */
.tooltip-content h3 {
  margin: 0 0 12px;
  font-size: 1.25rem;
  color: #333;
  text-align: center;
}

/* Close button styles */
.close-button {
  background-color: #ff4d4d;
  color: white;
  border: none;
  border-radius: 50%;
  padding: 8px;
  width: 32px;
  height: 32px;
  font-size: 0.7rem;
  line-height: 1;
  cursor: pointer;
  position: absolute;
  top: 12px;
  right: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease;
}

.close-button:hover {
  background-color: #e60000;
}

/* Style for the list of correct answers */
.tooltip-content ul,
.tooltip-content ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.tooltip-content li {
  margin-bottom: 8px;
  font-size: 0.95rem;
  color: #444;
}

.tooltip-content li {
  position: relative;
  padding-left: 20px; /* Space for marker */
  margin-bottom: 8px;
  font-size: 0.95rem;
  color: #444;
}

.tooltip-content li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 8px;
  height: 8px;
  border-radius: 50%; /* Circle marker */
  background-color: #ff4d4d; /* Custom color for the marker */
}
