@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined');


/* Courses Section */
.courses-base {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto 3rem;
  padding: 2rem 1rem;
}

.courses-manual {
  flex-basis: 40%;
  background: linear-gradient(135deg, #fff5e6, #fef9f5);
  border-radius: var(--border-radius);
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
  padding: 2rem;
  position: relative;
}

.courses-manual h1 {
  color: #000;
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  margin-top: 0;
  text-transform: uppercase;
}

.courses-manual-inf {
  font-size: 1rem;
  line-height: 1.6;
  color: #444;
  margin-top: 1rem;
}

/* Courses Grid */
.courses-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  position: relative;
  padding: 2rem 1rem;
  border-radius: var(--border-radius);
  
  background: linear-gradient(145deg, #ffffff, #e6e6e6);
  box-shadow: 10px 10px 30px rgba(0,0,0,0.1),
              -10px -10px 30px rgba(255,255,255,0.7);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,0.3);
}

/* Course Item */
.course-item {
  flex: 1 1 calc(33.333% - 2rem);
  max-width: calc(33.333% - 2rem);
  border-radius: var(--border-radius);
  background: linear-gradient(145deg, #ffffff, #f1f1f1);
  box-shadow: 6px 6px 12px #d1d1d1,
              -6px -6px 12px #ffffff;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  position: relative;
  overflow: hidden;
}

.course-item:hover {
  transform: translateY(-10px);
  box-shadow: 8px 8px 16px rgba(209,209,209,0.6),
              -8px -8px 16px #ffffff;
}

.image-container {
  position: relative;
  width: 100%;
  overflow: hidden;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.course-type {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #222;
  color: #fff;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.course-image {
  width: 100%;
  height: auto;
  border-radius: 10px;
  transition: transform 0.4s ease, filter 0.4s ease;
}

.course-image:hover {
  transform: scale(1.05);
  filter: brightness(1.2);
}

.course-item h2 {
  margin: 0.5rem 0;
  font-size: 1.5rem;
  color: #222;
  font-weight: 600;
  text-align: center;
  transition: color 0.3s ease;
  letter-spacing: 0.5px;
}

.course-item:hover h2 {
  color: var(--primary-color);
}

.course-item p {
  font-size: 1rem;
  text-align: center;
  color: #666;
  margin: 0.5rem 0 1rem;
  line-height: 1.4;
}

/* Overlay Details */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.6);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
}

.overlay.show {
  opacity: 1;
  pointer-events: auto;
}

.overlay-grid {
  display: grid;
  grid-template-columns: 1fr 2fr;
  background: #ffffff;
  padding: 2rem;
  border-radius: var(--border-radius);
  box-shadow: 0 10px 25px rgba(0,0,0,0.15);
  position: relative;
  max-width: 900px;
  width: 90%;
  gap: 1.5rem;
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 0.4s ease, transform 0.4s ease;
}

.overlay.show .overlay-grid {
  opacity: 1;
  transform: translateY(0);
}

.widener {
  grid-column: 1 / -1;
  text-align: center;
  font-size: 1.8rem;
  font-weight: 700;
  color: #333;
  background: linear-gradient(145deg, #f9f9f9, #ececec);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 4px 4px 10px #d1d1d1,
              -4px -4px 10px #ffffff;
}

.widener h2 {
  margin: 0;
  color: #000;
}

.left-grid {
  background: linear-gradient(145deg, #f8f8f8, #eaeaea);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  color: #444;
  box-shadow: 4px 4px 10px #d1d1d1,
              -4px -4px 10px #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.detail-image {
  width: 100%;
  height: auto;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.detail-heading {
  border-radius: 10px 20px 10px 20px;
  padding: 1rem;
  background-color: var(--primary-color);
  text-align: center;
  color: #fff;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 1rem 0 0;
  text-transform: uppercase;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
}

.right-grid {
  background: linear-gradient(145deg, #fff5e6, #f7e5d6);
  border-radius: var(--border-radius);
  padding: 2rem;
  color: #444;
  text-shadow: 1px 1px 4px rgba(0,0,0,0.1);
  box-shadow: 4px 4px 10px #d1d1d1,
              -4px -4px 10px #ffffff;
  position: relative;
  overflow-y: auto;
  max-height: 60vh;
}

/* Close Button */
.clsz {
  position: absolute;
  top: 8px;
  right: 8px;
  border: none;
  background: rgba(255,255,255,0.8);
  border-radius: 50%;
  padding: 0.3rem;
  cursor: pointer;
  transition: transform 0.3s ease, background 0.3s ease;
  z-index: 10;
}

.clsz:hover {
  transform: scale(1.2);
  background: rgba(255,255,255,1);
}

/* Responsive Adjustments */
@media (max-width: 800px) {
  .courses-base {
    flex-direction: column;
    gap: 1rem;
  }

  .courses-grid {
    gap: 1.5rem;
  }

  .course-item {
    flex: 1 1 100%;
    max-width: 100%;
  }

  .overlay-grid {
    grid-template-columns: 1fr;
  }

  .left-grid,
  .right-grid {
    padding: 1rem;
  }
}

@media (max-width: 500px) {
  h1 {
    font-size: 2rem;
  }

  .detail-heading {
    font-size: 1.2rem;
  }

  .course-item h2 {
    font-size: 1.2rem;
  }
}
.center {
text-align: center
} 